import Api from '@/common/api';
import coupon from '@/components/coolc-coupon/coolc-coupon';
import {
	formatDate
} from '@/common/date';
export default {
	components: {
		coupon
	},
	data() {
		return {
			keyword: '',
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			hotProductList: [],
			newProductList: [],
			randomProductList: [],
			loadFinish:false,
			page:{pageSize:6,pageNum:1}
		};
	},

	// onLoad() {
	// 	this.loadData();
	// 	this.loadRandomProduct();
	// },
	onShow() {
		this.loadData();
		this.loadRandomProduct();
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			let params = {};
			let data1 = await Api.apiCall('get', Api.goods.home, params);
			if (data1.code==0) {
				let data = data1.data;
				this.carouselList = data.advertiseList;
				this.swiperLength = this.carouselList.length;
				this.titleNViewBackground = 'rgb(203, 87, 60)';

				let hotProductList = data.hotProductList;
				this.hotProductList = hotProductList || [];
				
				let newProductList = data.newProductList;
				this.newProductList = newProductList || [];
			}
		},
		dateFormat(time) {
			if (time == null || time === '') {
				return 'N/A';
			}
			let date = new Date(time);
			return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		navToTabPage(url) {
			uni.navigateTo({
				url: url
			});
		},
		//详情页
		navToDetailPage(id) {
			//测试数据没有写id，用title代替
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			});
		},

		navToGroupDetailPage(item) {
			console.log(item);
			//测试数据没有写id，用title代替
			let id = item.goodsId;
			let groupId = item.id;

			uni.navigateTo({
				url: `/pages/product/groupProduct?id=${id}&&groupId=${item.id}`
			});
		},
		navToSkillDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/secskillDetail?id=${id}`
			});
		},
		navToList() {
			uni.navigateTo({
				url: `/pages/product/list`
			});
		},
		/**
		 * 加载随机产品
		 */
		async loadRandomProduct(){
			
			let data=await Api.apiCall("GET",Api.goods.randomProductList,this.page);
			if(data.code==0){
				let data1=data.data;
				this.randomProductList.push(...data1.records);
				if(data1.total<=this.randomProductList.length)this.loadFinish=true;
				this.page.pageNum++;
			}
		}
		,search() {
			uni.navigateTo({
				url: '/pages/search/search'
			});
		}
	},



	// 标题栏input搜索框点击
	onNavigationBarSearchInputClicked: async function(e) {
		uni.navigateTo({
			url: '/pages/search/search'
		});
	},
	//点击导航栏 buttons 时触发
	onNavigationBarButtonTap(e) {
		const index = e.index;
		if (index === 0) {
			this.$api.msg('点击了扫描');
		} else if (index === 1) {








			uni.navigateTo({
				url: '/pages/notice/notice'
			});
		}
		},
		onReachBottom() {
			if(!this.loadFinish)
		 this.loadRandomProduct();
		}
};
